@import url("https://fonts.googleapis.com/earlyaccess/notosansjp.css");

html,
body {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 62.5%;
}

p,
li,
span,
code {
  font-size: $fontSize;
}

button{
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  appearance: none;
}